import React from 'react'
import Img from 'gatsby-image'
import styled from 'styled-components'
import useLogos from '../hooks/use-logos'
import ScrollAnimation from 'react-animate-on-scroll'

const StyledAnimation = styled(ScrollAnimation)`
  margin-bottom: 0;
  :not(:last-child) {
    padding-right: 2rem;

    @media (max-width: 768px) {
      padding-right: 0;
    }
  }
`

const Logos = () => {
  const logos = useLogos()
  const delay = 150

  return (
    <section className="clients">
      <ScrollAnimation animateIn="fadeInUp">
        <h2 className="clients__header">Clients</h2>
      </ScrollAnimation>
      <div className="clients__wrapper">
        {logos.map((logo, idx) => {
          return (
            <StyledAnimation
              key={logo.logo.node.sharp.fixed.src}
              animateIn="growInUp"
              delay={delay * idx}
            >
              <Img fixed={logo.logo.node.sharp.fixed} backgroundColor="black" />
            </StyledAnimation>
          )
        })}
      </div>
    </section>
  )
}

export default Logos
