import React, { useRef, useState, useEffect } from 'react'
import ScrollAnimation from 'react-animate-on-scroll'
import axios from 'axios'
import { useForm } from 'react-hook-form'
import Animate from '../hooks/use-mount-animations'
import Loader from './loader'

const Contact = () => {
  const formRef = useRef(null)
  const [success, setSuccess] = useState(false)
  const [loading, setLoading] = useState(false)
  const [formHeight, setFormHeight] = useState(null)
  const [error, setError] = useState('')
  const { handleSubmit, register, errors } = useForm()

  useEffect(() => {
    if (!formRef) return

    if (formRef.current.offsetHeight)
      setFormHeight(formRef.current.offsetHeight)
  }, [formRef])

  const handleSubmitForm = async () => {
    setLoading(true)

    const url = formRef.current.action
    const data = new FormData(formRef.current)

    const fields = JSON.stringify([
      'first_name',
      'last_name',
      'email',
      'company',
      'message',
    ])

    data.append('formGoogleSheetName', 'responses')
    data.append('formDataNameOrder', fields)

    if (!url || !data) {
      return
    }

    const options = {
      method: 'post',
      url,
      data,
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded',
      },
    }

    try {
      const res = await axios(options)

      if (res.status === 200) {
        setSuccess(true)
        setLoading(false)
      } else {
        setError('Something went wrong')
      }
    } catch (err) {
      throw new Error(err)
    }
  }

  return (
    <section className="contact">
      <ScrollAnimation animateIn="sliceDaddy">
        <h2>
          <span className="sliceBaby">Get</span>{' '}
          <span className="sliceBaby">in</span>{' '}
          <span className="sliceBaby">touch</span>
        </h2>
      </ScrollAnimation>
      <Animate animIn="fadeInUp" animOut="fadeOutDown" show={success}>
        <ScrollAnimation animateIn="fadeInUp">
          <h3>Thanks for Reaching out! We'll be in touch!</h3>
        </ScrollAnimation>
      </Animate>
      <ScrollAnimation
        animateIn="fadeInUp"
        delay={150}
        style={{ height: formHeight ? formHeight : 'auto' }}
      >
        <Animate animIn="fadeInUp" animOut="fadeOutDown" show={!success}>
          <form
            id="general-form"
            method="post"
            className="blue gform"
            action="https://script.google.com/macros/s/AKfycbzbZGQDdHrYiz_du0u0ECa9HMpT6ZsGh3M8dKT5m8X0dbkyCz1j/exec"
            onSubmit={handleSubmit(handleSubmitForm)}
            ref={formRef}
          >
            <div className="input-group half">
              <div className="input-wrap">
                <input
                  type="text"
                  name="first_name"
                  placeholder="First name"
                  ref={register({
                    required: true,
                  })}
                />
                {errors.first_name && (
                  <span className="error-container">First name required</span>
                )}
                <span className="focus-outline"></span>
              </div>

              <div className="input-wrap">
                <input
                  type="text"
                  name="last_name"
                  placeholder="Last name"
                  ref={register({
                    required: true,
                  })}
                />
                {errors.last_name && (
                  <span className="error-container">Last name required</span>
                )}
                <span className="focus-outline"></span>
              </div>
            </div>

            <div className="input-group">
              <div className="input-wrap">
                <input
                  type="email"
                  id="email-field"
                  name="email"
                  placeholder="Email"
                  ref={register({
                    required: true,
                    pattern: {
                      value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                      message: 'invalid email address',
                    },
                  })}
                />
                {errors.email && (
                  <span className="error-container">
                    A valid email required
                  </span>
                )}
                <span className="focus-outline"></span>
              </div>
            </div>

            <div className="input-group">
              <div className="input-wrap">
                <input
                  type="text"
                  name="company"
                  placeholder="Company Name"
                  ref={register({
                    required: true,
                  })}
                />
                {errors.company && (
                  <span className="error-container">Company name required</span>
                )}
                <span className="focus-outline"></span>
              </div>
            </div>

            <div className="input-group">
              <div className="input-wrap">
                <textarea
                  name="message"
                  placeholder="What's on your mind?"
                  ref={register({
                    required: true,
                  })}
                ></textarea>
                {errors.message && (
                  <span className="error-container">
                    Please leave us a message
                  </span>
                )}
                <span className="focus-outline"></span>
              </div>
            </div>

            <div
              className="input-wrap btn-circle"
              style={{ position: 'relative', border: 'none' }}
            >
              {loading ? (
                <Loader />
              ) : (
                <input
                  type="submit"
                  className="btn-circle filler"
                  value="submit"
                />
              )}
            </div>
            <fieldset disabled={loading} hidden={true} />
          </form>
          {error && <span className="error">{error}</span>}
        </Animate>
      </ScrollAnimation>
    </section>
  )
}

export default Contact
